<template>
  <div class="container">

    <h2>{{ blog.title }}</h2> 
    <h6>作者：{{ blog.author }}</h6>
    <h6>時間：{{ blog.time }}</h6>
    <h6>分類：{{ blog.subject }}</h6>

    <div v-if="blog.video" class="video-container" v-html="blog.video"/>
    <v-md-editor v-model="blog.content" mode="preview" @copy-code-success="handleCopyCodeSuccess"/>
    <br><br><br>

  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      rows: [],
      cookieName: 'id',
      timeid: '',
      blog: '',   
    };
  },
  mounted() {
    this.timeid=this.$route.params.id;

    // 從 Google Sheets 取得資料，並找出指定時間的文章
    axios.get('https://script.google.com/macros/s/AKfycbxd9TQCEQ-PiVCj2zSN-TLGxw-GB2l4ppf1GcdKhS0yxRMr-PqWeqOtrkzwRWqoj1Q3/exec')
      .then(response => {
        this.rows = response.data;
        this.blog = this.rows.find(row => row.time == this.timeid);
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    handleCopyCodeSuccess(code) {
      alert('複製成功！');
      console.log(code);
    },
  },
};

</script>

<style>
.video-container {
  aspect-ratio: 16/9;
}

