<template>
  <div class="container">
    <h2>註冊表單</h2>
    <form @submit.prevent="submitForm">
      <div class="form-group">
        <label for="username">帳號 *</label>
        <input type="text" id="username" v-model.trim="form.username" pattern="[A-Za-z0-9]+" maxlength="10"
          placeholder="請輸入由英文以及數字構成且長度小於10的帳號名稱" required>
      </div>
      <div class="form-group">
        <label for="password">密碼 *</label>
        <input type="password" id="password" v-model.trim="form.password" pattern="[A-Za-z0-9]+" maxlength="10"
          placeholder="請輸入由英文以及數字構成且長度小於10的密碼" required>
      </div>
      <div class="form-group">
        <label for="confirmPassword">確認密碼 *</label>
        <input type="password" id="confirmPassword" v-model.trim="form.confirmPassword" required>
      </div>
      <div class="form-group">
        <label for="realName">真實姓名 *</label>
        <input type="text" id="realName" v-model.trim="form.realName" required>
      </div>
      <div class="form-group">
        <label for="email">電子郵件</label>
        <input type="email" id="email" v-model.trim="form.email">
      </div>
      <div class="form-group">
        <label for="phone">電話號碼</label>
        <input type="tel" id="phone" v-model.trim="form.phone">
      </div>
      <div class="form-group">
        <label for="mood">心情留言</label>
        <textarea id="mood" v-model.trim="form.mood"></textarea>
      </div>
      <input type="submit" :value="register">
    </form>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
        confirmPassword: '',
        realName: '',
        email: '',
        phone: '',
        mood: '',
        status: 'normal'
      },
      register: '註冊'
    };
  },
  methods: {
    async submitForm() {
      this.register = '處理中，請稍後';

      // 檢查帳號是否已存在
      try {
        const response = await axios.get('https://script.google.com/macros/s/AKfycbyGb1Ti5VO37f3z7bNjmmf9FMoa43zlRJXFnyemP_s0T-qSyrFzmwmkmCXgdmkoduZb/exec');
        const rows = response.data;
        const usrname = rows.find((row) => row.username === this.form.username);
        if (usrname) {
          alert('帳號已存在');
          location.reload();
          return;
        }
      } catch (error) {
        alert(error);
        return;
      }

      // 檢查密碼是否相同
      if (this.form.password !== this.form.confirmPassword) {
        alert('確認密碼與密碼不相同，請重新輸入資料');
        return;
      }

      // 執行註冊
      const scriptURL = 'https://script.google.com/macros/s/AKfycbxYt8auCq1IBMxVRDsGs9NQnzlyAPw7q3w1_gnd7N8ix0MBb18YKaXFWg5wGvDaZd2l/exec';
      const form = new FormData();
      form.append('time', new Date().toLocaleString());
      form.append('username', this.form.username);
      form.append('password', this.form.password);
      form.append('status', this.form.status);
      form.append('realName', this.form.realName);
      form.append('email', this.form.email);
      form.append('phone', this.form.phone);
      form.append('mood', this.form.mood);

      try {
          await fetch(scriptURL, { method: 'POST', body: form });
          alert('註冊成功！！！');
          alert('登入成功！！！');
          this.$store.dispatch('setLogin', true);
          this.$store.dispatch('setLogin',this.form.username);
          this.$router.push('/');
        } catch (error) {
          console.error('Error!', error.message);
        }
      }
    }
  }
</script>

