<template>
  <div>
    <b-container>
      <b-col class="m-5 text-center">
        <h2>These are my blogs</h2>
      </b-col>
      <b-row align-h="center" class="m-3">
        <table class="table table-striped table-hover" >
          <thead>
            <tr>
              <th scope="col">標題</th>
              <th scope="col">作者</th>
              <th scrpe="col">摘要</th> 
              <th scrpe="col">全文連結</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="blog in rows" :key="blog.timestamp">
              <td>{{ blog.title }}</td>
              <td>{{ blog.author }}</td>
              <td>{{ blog.summary }}</td>
              <td><button @click="redir(blog.time)">查看全文</button></td>
            </tr>
          </tbody>
        </table>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      headers: [],
      rows: []
    };
  },
  mounted() {
    axios.get('https://script.google.com/macros/s/AKfycbxd9TQCEQ-PiVCj2zSN-TLGxw-GB2l4ppf1GcdKhS0yxRMr-PqWeqOtrkzwRWqoj1Q3/exec')
      .then(response => {
        this.headers = Object.keys(response.data[0]);
        this.rows = response.data;
      })
      .catch(error => {
        console.log(error);
      });
  },
  methods: {
    redir(id){
      this.$router.push("/show/"+id);
    }
  }
};
</script>
