<template>
  <div class="container">
    <form @submit.prevent="submitForm">
      <label for="title">標題</label>
      <input v-model="form.title" type="text" placeholder="請輸入標題" required>

      <label for="author">作者</label>
      <input v-model="form.author" type="text" placeholder="請輸入作者" disabled>

      <label for="summary">摘要</label>
      <input v-model="form.summary" type="text" placeholder="摘要請勿超過50字" maxlength="50" required>
      
      <label for="content">內容(建議放大後編輯)</label>
      <v-md-editor v-model="form.content" style="height: 500px;"/>

      <br>
      <label for="subject">科目</label>
      <select v-model="form.subject">
        <option v-for="(option, index) in subjectlist" :key="index" :value="option">{{ option }}</option>
      </select>
      <label for="videos">影片</label>
      <input v-model="form.video" type="text" placeholder="請貼上youtube分享的嵌入碼">
      
      <label for="hashtags">HashTags</label>
      <input v-model="form.tags" type="text" placeholder='請輸入標籤，請以","間隔'/>
      <input type="submit" value="Submit" name="submit">
      <p v-if="showText">處理中，請稍候</p>
      <br> <br>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        title: '',
        author: this.$store.getters.getName,
        summary: '',
        content: '',
        video: '',
        subject: '',
        tags: '',
      },
      subjectlist: ['國文','數學','英文','物理','化學','生物','地科','資訊','社會','其他'],
      showText: 0,
    }
  },

  mounted(){
    if(this.$store.getters.getLogin==false){
      alert("請先登入");
      this.$router.push("/login");
    }
  },
  methods: {
    submitForm() {
      this.showText=1;
      const scriptURL='https://script.google.com/macros/s/AKfycbz2t22igsmipa81bgWNJkrld95jvUVjJ7aTBELNmKIomIcXskf8yLn3JB5NwMcJSG13/exec'
      const form = new FormData();
      form.append('time', new Date().toLocaleString()); 
      form.append('title', this.form.title);
      form.append('author', this.form.author);
      form.append('summary', this.form.summary);
      form.append('content', this.form.content);
      form.append('subject',this.form.subject);
      form.append('tags',this.form.tags);
      form.append('video',this.form.video);
      fetch(scriptURL, { method: 'POST', body: form })
      // eslint-disable-next-line no-unused-vars
        .then(response => {
          alert("上傳成功！！！");
          this.$router.push('/blogs');
        })
        .catch(error => console.error('Error!', error.message));
    }
  }
}
</script>

